import React from 'react'
import * as bs from 'react-bootstrap'
import { BrowserRouter as Router, Switch, Route } from "react-router-dom"
import HeaderContainer from './header-container'
import LeftContainer from './left-container'
import RightContainer from './right-container'
import FooterContainer from './footer-container'
import Home from './home'
import Search from './search'
import PredictionCalc from './calc'
import './App.scss'
import SearchResults from './searchResults'
import SortBy from './sortBy'
import CampaignDetails from './campaignDetails'
import PredictionResults from './predictionResults'

function App(props) {
    return (
        <Router>
            <bs.Container fluid className="p-0 min-vh-100 d-flex flex-column">
                <bs.Row noGutters className="flex-grow-0 flex-shrink-0 border-bottom shadow-sm">
                    <bs.Col className="px-3 py-2">
                        <HeaderContainer />
                    </bs.Col>
                </bs.Row>
                <bs.Row noGutters className="flex-grow-1">
                    <bs.Col md="2" className="px-3 py-4 border-right">
                        <LeftContainer />
                    </bs.Col>
                    <bs.Col md="8">
                        <Switch>
                            <Route path="/search">
                                <Search />
                            </Route>
                            <Route path="/calc">
                                <PredictionCalc />
                            </Route>
                            <Route path="/searchResults">
                                <SearchResults />
                            </Route>
                            <Route path="/campaigns/:id">
                                <CampaignDetails />
                            </Route>
                            <Route path="/sortBy">
                                <SortBy />
                            </Route>
                            <Route path="/predictionResults">
                                <PredictionResults />
                            </Route>
                            <Route path="/">
                                <Home />
                            </Route>
                        </Switch>
                    </bs.Col>
                    <bs.Col md="2" className="px-3 py-4 border-left">
                        <RightContainer />
                    </bs.Col>
                </bs.Row>
                <bs.Row noGutters className="flex-grow-0 flex-shrink-0">
                    <bs.Col className="px-3 py-2">
                        <FooterContainer />
                    </bs.Col>
                </bs.Row>
            </bs.Container>
        </Router>
    )
}
export default App
