import React from 'react'
import { useParams } from 'react-router-dom'
import AppContext from './context'

function CampaignDetails(props) {
    const context = React.useContext(AppContext)
    let { id } = useParams()
    let myCamp = {}
    let isFound = false

    let campaignsArray = Object.values(context.campaigns)

    console.log(campaignsArray)

    for (var itemInner of campaignsArray) {
        if (itemInner.campaign_id === id) {
            myCamp = itemInner
            isFound = true
        }
    }


    if (isFound === false){
        return(<h1>Not Found</h1>)
    }
    

    return(
        <div>
            <h1>{myCamp['title']}</h1>
            <img src={myCamp['campaign_image_url']} alt=''></img>
            <p>Name: {myCamp['user_first_name']} {myCamp['user_last_name']}</p>
            <p>URL: <a href={myCamp['url']}>{myCamp['url']}</a></p>
            <p>Current Amount: {myCamp['current_amount']}</p>
            <p>Goal: {myCamp['goal']}</p>
            <p>Percent Complete: {myCamp['percent_goal_complete']}</p>
            <p>Number of Donors: {myCamp['donators']}</p>
            <p>Average Donation per Doner: {myCamp['avg_donation_per_doner']}</p>
            <p>Creation Date: {myCamp['created_at']}</p>
            <p>Launch Date: {myCamp['launch_date']}</p>
            <p>Days Active: {myCamp['days_active']}</p>
            <p>Days Since Creation: {myCamp['days_created']}</p>
            <p>Description: {myCamp['description']}</p>
            <p>Number of Campaign Hearts {myCamp['campaign_hearts']}</p>
            <p>Total Number of Shares on Social Media: {myCamp['social_share_total']} (Last updated: {myCamp['social_share_last_update']})</p>
            <p>Overall Popularity: {myCamp['overall_popularity']}</p>
            <p>City: {myCamp['location_city']}</p>
            <p>Country: {myCamp['location_country']}</p>
            <p>Zip Code: {myCamp['location_zip']}</p>
            <p>Is this a charity?: {myCamp['is_charity']}</p>

        </div>
    )
}
export default CampaignDetails