import React from 'react'
import * as bs from 'react-bootstrap'
import { Formik, Form, Field, useField} from 'formik'
import AppContext from './context'
import axios from 'axios'
import { useHistory } from 'react-router-dom'
import SearchResults from './searchResults'

function PredicitonCalc(props) {
    return (
        <bs.Container>
            <SearchController />
        </bs.Container>
    )
}
export default PredicitonCalc

const SearchController = props => {
    const context = React.useContext(AppContext)
    const history = useHistory()    

    return (
        <Formik
            initialValues={{
                campaign_id: '',
                goal: 0,
                title: '',
                description: '',
                has_benificiary: false,
                visible_in_search: false,
                is_charity: false,
                charity_npo_id: '',            
                auto_fb_post_mode: false,
                user_facebook_id: false
            }}
            validateOnChange={false}
            validateOnBlur={false}
            validate={values => {
                const errors = {}
                //console.log('validating', values)
                if (!values.goal){
                    errors.goal = "Please enter your campaign goal"
                }
                if (!values.title){
                    errors.title = "Please enter your campaign title"
                }
                if (!values.description){
                    errors.description = "Please enter your campaign description"
                }
                if (values.description_language === "Select"){
                    errors.description_language = "Please select a language"
                }
                if (values.currencycode === "Select"){
                    errors.description_language = "Please select a language"
                }
                if (values.location_country === "Select"){
                    errors.description_language = "Please select a language"
                }
                return errors
            }}
            onSubmit={async (values, actions) => {
                console.log('submit', values)
                const resp = await axios.get('http://ec2-52-201-219-100.compute-1.amazonaws.com/api/predictionCalculator/', {
                // const resp = await axios.get('http://localhost:8000/api/predictionCalculator/', {
                    params: {
                        goal: values.goal,
                        title: values.title,
                        description: values.description,
                        has_benificiary: values.has_benificiary,
                        visible_in_search: values.visible_in_search,
                        is_individual: values.is_individual,
                        is_charity: values.is_charity,
                        charity_npo_id: values.charity_npo_id,
                        description_language: values.description_language,
                        auto_fb_post_mode: values.auto_fb_post_mode,
                        currencycode: values.currencycode,
                        user_facebook_id: values.user_facebook_id,
                        location_country: values.location_country
                    }                    
                })
                console.log(resp.data)
                history.push({
                    pathname: "/predictionResults",
                    search: "",
                    state: {detail: resp.data}
                })
                return (<SearchResults values={values} actions={actions}/>)
            }}
        >{form => (
            <CalcForm form={form}/>
        )}</Formik>
    )
}

