import React from 'react'
import * as bs from 'react-bootstrap'
import { useLocation } from 'react-router-dom'
import { useEffect } from 'react'
import CampaignCard from './campaignCard'

const NUM_COLUMNS = 4

function SearchResults(props) {
    return (
        <bs.Container>
            <SearchController />
        </bs.Container>
    )
}
export default SearchResults


const SearchController = props => {
    const location = useLocation();

    var results = []

    useEffect(() => {
        console.log(location.state.detail)
        results = location.state.detail;        
    })

    results = (location.state.detail)

    // What do they want to sort by?
    var sortBy = location.state.sortBy;

    if (sortBy == 'avg_donation_per_donor') {
        results.sort((a, b) => (parseFloat(a.avg_donation_per_donor) < parseFloat(b.avg_donation_per_donor)) ? 1 : -1 )
    } else if (sortBy === 'percent_goal_complete') {
        results.sort((a, b) => (parseFloat(a.percent_goal_complete) < parseFloat(b.percent_goal_complete)) ? 1 : -1 )
    } else if (sortBy === 'campaign_hearts') {
        results.sort((a, b) => (parseFloat(a.campaign_hearts) < parseFloat(b.campaign_hearts)) ? 1 : -1 )
    } else if (sortBy === 'social_share_total') {
        results.sort((a, b) => (parseFloat(a.social_share_total) < parseFloat(b.social_share_total)) ? 1 : -1 )
    } else if (sortBy === 'overall_popularity') {
        results.sort((a, b) => (parseFloat(a.overall_popularity) < parseFloat(b.overall_popularity)) ? 1 : -1 )
    } else {
        results.sort((a, b) => (parseFloat(a.current_amount) < parseFloat(b.current_amount)) ? 1 : -1 )
    }
    
    // slice the products into an array of arrays (e.g. a list of rows of 4 products each)
    const rows = []
    for (let i = 0; i < results.length; i += NUM_COLUMNS) {
        rows.push(results.slice(i, i + NUM_COLUMNS))
    }

    return (
        rows.map((row, row_idx) => (
            <bs.Row key={row_idx}>
                {row.map(campaign => (
                    <bs.Col md="3" key={campaign.campaign_id}>
                        <CampaignCard campaign={campaign} sortBy={sortBy}/>
                    </bs.Col>
                ))}
            </bs.Row>
        ))    
    )
}